import { useMemo } from 'react';
import {
  Chart,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Tooltip,
  Legend,
} from 'chart.js';
import { Line } from 'react-chartjs-2';

Chart.register(CategoryScale, LinearScale, PointElement, LineElement, Tooltip, Legend);

const buildDataset = (metric) => {
  const labels = metric.series.map((point) => point.week);
  const data = metric.series.map((point) => point.value);

  const datasets = [
    {
      label: metric.name,
      data,
      borderColor: '#2563eb',
      backgroundColor: 'rgba(37, 99, 235, 0.15)',
      borderWidth: 2,
      fill: true,
      spanGaps: true,
      tension: 0.35,
      pointRadius: 3,
    },
  ];

  if (metric.targetValue !== null && metric.targetValue !== undefined) {
    datasets.push({
      label: 'Goal',
      data: labels.map(() => metric.targetValue),
      borderColor: '#9ca3af',
      borderDash: [6, 6],
      borderWidth: 1,
      pointRadius: 0,
      fill: false,
    });
  }

  return { labels, datasets };
};

export default function MetricChart({ metric }) {
  const chartConfig = useMemo(() => buildDataset(metric), [metric]);

  return (
    <div className="metric-chart">
      <Line
        data={chartConfig}
        options={{
          responsive: true,
          plugins: {
            legend: { position: 'top' },
            tooltip: {
              mode: 'index',
              intersect: false,
              callbacks: {
                label: (context) => {
                  const label = context.dataset.label || '';
                  const value = context.parsed.y;
                  if (value === null) return `${label}: —`;
                  return `${label}: ${value}`;
                },
              },
            },
          },
          interaction: { intersect: false, mode: 'index' },
          scales: {
            y: {
              beginAtZero: false,
              grid: { color: 'rgba(75,85,99,0.15)' },
              ticks: { callback: (value) => value },
            },
            x: {
              grid: { display: false },
            },
          },
        }}
      />
    </div>
  );
}
