import { useEffect, useMemo, useRef, useState } from 'react';
import MetricCard from '@/components/MetricCard';
import MetricChart from '@/components/MetricChart';
import UploadPanel from '@/components/UploadPanel';
import { filterMetrics, getMetricCategories, STATUS_ORDER } from '@/lib/metrics';

const formatTimestamp = (timestamp) => {
  try {
    return new Date(timestamp).toLocaleString();
  } catch {
    return timestamp;
  }
};

const timeAgoLabel = (timestamp) => {
  if (!timestamp) return 'Unknown';
  const target = new Date(timestamp);
  if (Number.isNaN(target.getTime())) return 'Unknown';
  const now = new Date();
  const diffMs = now.getTime() - target.getTime();
  if (diffMs < 0) return 'Just now';
  const minutes = Math.floor(diffMs / 60000);
  if (minutes < 1) return 'Just now';
  if (minutes < 60) return `${minutes} min ago`;
  const hours = Math.floor(minutes / 60);
  if (hours < 24) return `${hours} hr${hours > 1 ? 's' : ''} ago`;
  const days = Math.floor(hours / 24);
  if (days < 30) return `${days} day${days > 1 ? 's' : ''} ago`;
  const months = Math.floor(days / 30);
  if (months < 12) return `${months} mo ago`;
  const years = Math.floor(months / 12);
  return `${years} yr${years > 1 ? 's' : ''} ago`;
};
const getLatestValue = (metric) => metric.latest?.display || metric.latest?.raw || '—';

const STORAGE_KEYS = {
  theme: 'scorecard-theme',
  search: 'scorecard-search',
  categories: 'scorecard-categories',
  statuses: 'scorecard-statuses',
};

const isBrowser = () => typeof window !== 'undefined';

const readStoredArray = (key) => {
  if (!isBrowser()) return null;
  try {
    const raw = window.localStorage.getItem(key);
    if (!raw) return null;
    const parsed = JSON.parse(raw);
    return Array.isArray(parsed) ? parsed : null;
  } catch {
    return null;
  }
};

const writeStoredValue = (key, value) => {
  if (!isBrowser()) return;
  try {
    window.localStorage.setItem(key, value);
  } catch {
    /* ignore storage errors */
  }
};

const writeStoredArray = (key, value) => {
  if (!isBrowser()) return;
  try {
    window.localStorage.setItem(key, JSON.stringify(value));
  } catch {
    /* ignore storage errors */
  }
};

const statusLabels = {
  'on-track': 'On Track',
  'at-risk': 'At Risk',
  unknown: 'Unknown',
};

export default function ScorecardDashboard({ initialData }) {
  const [payload, setPayload] = useState(initialData);
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedCategories, setSelectedCategories] = useState([]);
  const [selectedStatuses, setSelectedStatuses] = useState(STATUS_ORDER);
  const [selectedMetricId, setSelectedMetricId] = useState(
    initialData?.metrics?.[0]?.id || null,
  );
  const [isRefreshing, setIsRefreshing] = useState(false);
  const [refreshError, setRefreshError] = useState('');
  const [theme, setTheme] = useState('light');

  const savedFilters = useRef({ categories: null, statuses: null });

  const metrics = useMemo(
    () => (Array.isArray(payload?.metrics) ? payload.metrics : []),
    [payload],
  );
  const generatedAt = payload?.generatedAt;
  const lastUpdatedAt = payload?.lastUpdatedAt;
  const cacheInfo = payload?.cacheInfo;

  const orderedMetrics = useMemo(() => {
    const list = Array.isArray(metrics) ? metrics : [];
    return [...list].sort((a, b) => {
      if (a.category === b.category) {
        return a.name.localeCompare(b.name);
      }
      return a.category.localeCompare(b.category);
    });
  }, [metrics]);

  const categories = useMemo(() => getMetricCategories(orderedMetrics), [orderedMetrics]);

  useEffect(() => {
    if (!isBrowser()) return;
    const savedTheme = window.localStorage.getItem(STORAGE_KEYS.theme);
    if (savedTheme) setTheme(savedTheme);
    const savedSearch = window.localStorage.getItem(STORAGE_KEYS.search);
    if (savedSearch) setSearchTerm(savedSearch);
    const savedStatuses = readStoredArray(STORAGE_KEYS.statuses);
    if (savedStatuses?.length) {
      savedFilters.current.statuses = null;
      setSelectedStatuses(savedStatuses);
    }
    const savedCategories = readStoredArray(STORAGE_KEYS.categories);
    if (savedCategories?.length) {
      savedFilters.current.categories = savedCategories;
    }
  }, []);

  useEffect(() => {
    if (!categories.length) return;
    setSelectedCategories((prev) => {
      const pending = savedFilters.current.categories;
      if (pending) {
        const valid = pending.filter((category) => categories.includes(category));
        savedFilters.current.categories = null;
        if (valid.length) return valid;
      }
      if (!prev || !prev.length) return categories;
      const filtered = prev.filter((category) => categories.includes(category));
      return filtered.length ? filtered : categories;
    });
  }, [categories]);

  useEffect(() => {
    if (typeof document === 'undefined') return;
    document.documentElement.dataset.theme = theme;
    return () => {
      document.documentElement.dataset.theme = 'light';
    };
  }, [theme]);

  const filteredMetrics = useMemo(
    () =>
      filterMetrics(orderedMetrics, {
        term: searchTerm,
        categories: selectedCategories,
        statuses: selectedStatuses,
      }),
    [orderedMetrics, searchTerm, selectedCategories, selectedStatuses],
  );

  useEffect(() => {
    if (!filteredMetrics.length) {
      setSelectedMetricId(null);
      return;
    }
    const stillVisible = filteredMetrics.find((metric) => metric.id === selectedMetricId);
    if (!stillVisible) {
      setSelectedMetricId(filteredMetrics[0].id);
    }
  }, [filteredMetrics, selectedMetricId]);

  const selectedMetric =
    filteredMetrics.find((metric) => metric.id === selectedMetricId) || filteredMetrics[0] || null;
  const activeMetricId = selectedMetric?.id || '';
  const heroMetrics = filteredMetrics.slice(0, 6);
  const hasMetrics = orderedMetrics.length > 0;

  const handleToggleCategory = (category) => {
    if (!category) {
      setSelectedCategories(categories);
      return;
    }
    setSelectedCategories((prev) => {
      if (!prev.length) return [category];
      if (prev.includes(category)) {
        const next = prev.filter((item) => item !== category);
        return next.length ? next : categories;
      }
      return [...prev, category];
    });
  };

  const handleToggleStatus = (status) => {
    setSelectedStatuses((prev) => {
      if (prev.includes(status)) {
        const next = prev.filter((item) => item !== status);
        return next.length ? next : STATUS_ORDER;
      }
      return [...prev, status];
    });
  };

  const handleRefresh = async () => {
    setIsRefreshing(true);
    setRefreshError('');
    try {
      const response = await fetch(`/api/scorecard?ts=${Date.now()}`);
      const result = await response.json();
      if (!response.ok) {
        throw new Error(result.message || 'Unable to refresh data');
      }
      setPayload(result);
      setSelectedMetricId(result.metrics?.[0]?.id || null);
    } catch (error) {
      setRefreshError(error.message);
    } finally {
      setIsRefreshing(false);
    }
  };

  const handleThemeToggle = () => {
    setTheme((prev) => (prev === 'light' ? 'dark' : 'light'));
  };

  const handleUploadSuccess = (data) => {
    if (!data) return;
    setPayload(data);
    setSelectedMetricId(data.metrics?.[0]?.id || null);
    setRefreshError('');
  };

  useEffect(() => {
    writeStoredValue(STORAGE_KEYS.theme, theme);
  }, [theme]);

  useEffect(() => {
    writeStoredValue(STORAGE_KEYS.search, searchTerm);
  }, [searchTerm]);

  useEffect(() => {
    if (!selectedStatuses?.length) return;
    writeStoredArray(STORAGE_KEYS.statuses, selectedStatuses);
  }, [selectedStatuses]);

  useEffect(() => {
    if (!selectedCategories?.length) return;
    writeStoredArray(STORAGE_KEYS.categories, selectedCategories);
  }, [selectedCategories]);

  if (!hasMetrics) {
    return (
      <div className="error-panel">
        <h1>No metrics available</h1>
        <p>Verify that the spreadsheet contains the “Weekly EOS Scorecard” worksheet.</p>
      </div>
    );
  }

  return (
    <div className="dashboard">
      <header className="dashboard__header">
        <div>
          <p className="eyebrow">Weekly EOS Scorecard</p>
          <h1>IT Scorecard Overview</h1>
          <p className="muted">Rendering data pulled directly from the Excel workbook.</p>
        </div>
        <div className="timestamp">
          <div>
            <p>Last refreshed</p>
            <strong>{formatTimestamp(generatedAt)}</strong>
            <span className="freshness-label">{timeAgoLabel(generatedAt)}</span>
          </div>
          <div>
            <p>Data last updated</p>
            <strong>{formatTimestamp(lastUpdatedAt || generatedAt)}</strong>
            <span className="freshness-label">{timeAgoLabel(lastUpdatedAt || generatedAt)}</span>
          </div>
          {cacheInfo?.status && (
            <span className="cache-indicator">
              Cache&nbsp;{cacheInfo.status}
              {cacheInfo.mode ? ` (${cacheInfo.mode})` : ''}
            </span>
          )}
        </div>
      </header>

      <section className="dashboard__controls">
        <div className="control-group">
          <label htmlFor="search-input">Search</label>
          <input
            id="search-input"
            type="search"
            placeholder="Search metrics or categories"
            value={searchTerm}
            onChange={(event) => setSearchTerm(event.target.value)}
          />
        </div>
        <div className="control-group">
          <div className="control-group__label-row">
            <label>Categories</label>
            <button type="button" className="link-button" onClick={() => handleToggleCategory(null)}>
              Reset
            </button>
          </div>
          <div className="pill-group">
            {categories.map((category) => (
              <button
                key={category}
                type="button"
                className={`pill ${selectedCategories.includes(category) ? 'pill--active' : ''}`}
                onClick={() => handleToggleCategory(category)}
              >
                {category}
              </button>
            ))}
          </div>
        </div>
        <div className="control-group">
          <div className="control-group__label-row">
            <label>Status</label>
            <button
              type="button"
              className="link-button"
              onClick={() => setSelectedStatuses(STATUS_ORDER)}
            >
              Reset
            </button>
          </div>
          <div className="pill-group">
            {STATUS_ORDER.map((status) => (
              <button
                key={status}
                type="button"
                className={`pill ${selectedStatuses.includes(status) ? 'pill--active' : ''}`}
                onClick={() => handleToggleStatus(status)}
              >
                {statusLabels[status]}
              </button>
            ))}
          </div>
        </div>
        <div className="control-actions">
          <button type="button" className="primary-button" onClick={handleRefresh} disabled={isRefreshing}>
            {isRefreshing ? 'Refreshing…' : 'Refresh data'}
          </button>
          <button type="button" className="ghost-button" onClick={handleThemeToggle}>
            Toggle {theme === 'light' ? 'dark' : 'light'} mode
          </button>
        </div>
        {refreshError && <p className="error-text">{refreshError}</p>}
      </section>

      <section>
        <UploadPanel onSuccess={handleUploadSuccess} />
      </section>

      <section>
        <h2>Key Metrics</h2>
        {!heroMetrics.length && <p className="muted">No metrics match the current filters.</p>}
        <div className="metric-card-grid">
          {heroMetrics.map((metric) => (
            <MetricCard key={metric.id} metric={metric} />
          ))}
        </div>
      </section>

      <section className="metric-detail">
        <div className="metric-detail__header">
          <div>
            <h2>Metric Detail</h2>
            <p className="muted">Select a metric to visualize its weekly trend.</p>
          </div>
          <select value={activeMetricId} onChange={(event) => setSelectedMetricId(event.target.value)}>
            {filteredMetrics.map((metric) => (
              <option key={metric.id} value={metric.id}>
                {metric.category} — {metric.name}
              </option>
            ))}
          </select>
        </div>
        {selectedMetric ? (
          <>
            <MetricChart metric={selectedMetric} />
            <div className="metric-detail__table-wrapper">
              <table>
                <thead>
                  <tr>
                    <th>Week</th>
                    <th>Value</th>
                  </tr>
                </thead>
                <tbody>
                  {selectedMetric.series.map((point) => (
                    <tr key={`${selectedMetric.id}-${point.week}`}>
                      <td>{point.week}</td>
                      <td>{point.raw || '—'}</td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </>
        ) : (
          <p className="muted">No metric selected.</p>
        )}
      </section>

      <section>
        <h2>All Metrics</h2>
        <div className="metric-table-wrapper">
          <table>
            <thead>
              <tr>
                <th>Category</th>
                <th>Metric</th>
                <th>Goal</th>
                <th>Latest Week</th>
                <th>Latest Value</th>
                <th>Status</th>
              </tr>
            </thead>
            <tbody>
              {filteredMetrics.map((metric) => (
                <tr key={`table-${metric.id}`}>
                  <td>{metric.category}</td>
                  <td>{metric.name}</td>
                  <td>{metric.goal.goal || metric.goal.text || '—'}</td>
                  <td>{metric.latest.week}</td>
                  <td>{getLatestValue(metric)}</td>
                  <td className={`status-badge status-${metric.status || 'unknown'}`}>
                    {metric.status || 'unknown'}
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
          {!filteredMetrics.length && <p className="muted">No metrics match the current filters.</p>}
        </div>
      </section>
    </div>
  );
}
